.registration--main {
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 40px;
  justify-items: flex-start;
  align-items: center;
  padding: 3rem 0.8rem;
  background-color: #fffcf6;
}
.registration__svg__container {
  display: none;
}
.registration__svg__container img {
  height: 32rem;
  display: block;
  min-width: 100%;
}
.registration__form {
  width: 100%;
  margin: 0 auto;
  padding: 1.5rem 1rem;
  border-radius: 3px;
  box-shadow: 0 0 2px rgba(0, 0, 0, 0.3);
  background-color: Var(--color-white);
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 0.3rem;
  font-family: var(--primary-font);
  color: var(--input-label-color);
}
.registration__form legend {
  line-height: 1rem;
  font-size: 1.3rem;
  font-weight: 600;
  font-family: var(--tertiary-font);
  color: var(--legend-color);
  margin-bottom: 0.75rem;
  text-decoration: 2px underline currentColor;
  text-underline-offset: 2px;
}

.form__group {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
}
.form__group label {
  font-size: 0.9rem;
  font-weight: 600;
  line-height: 1.2rem;
}
.form__group input {
  font-family: inherit;
  font-size: 0.95rem;
  width: 100%;
  padding: 0.45rem 0.5rem;
  border: 1px solid var(--input-field-border);
  border-radius: 2px;
  outline: none;
  border-radius: 3px;
}
.form__group input:focus {
  outline: 1px solid var(--color-black);
}
.form__recaptcha__group {
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
}
.form__submit__group input {
  padding: 0.75rem 1.25rem;
}

@media screen and (min-width: 481px) {
  .registration__form {
    width: 90%;
    padding: 1.5rem;
  }
}
@media screen and (min-width: 760px) {
  .registration__form {
    width: 80%;
  }
  .registration--main {
    padding: 3rem;
  }
}
@media screen and (min-width: 992px) {
  .registration--main {
    grid-template-columns: repeat(2, 1fr);
  }
  .registration__form {
    width: 100%;
  }
  .registration__svg__container {
    display: block;
  }
}